}

.campaign__swiper-button-wrap .swiper-button-prev,
.campaign__swiper-button-wrap .swiper-button-next {
  position: absolute;
  top: 45%;
  transform: translateY(-45%);
}

.campaign__swiper-button-wrap .swiper-button-prev::after,
.campaign__swiper-button-wrap .swiper-button-next::after {
  font-size: 1rem;
  color: #131e29;
}

.campaign__swiper-button-wrap .swiper-pagination {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  color: #131e29;
  width: auto;
}

.campaign__swiper-button-wrap .swiper-button-prev {
  left: calc(50% - 4.375rem);
}

.campaign__swiper-button-wrap .swiper-button-next {
  left: calc(50% + 3.125rem);
}

.campaign__swiper-button-wrap .swiper-pagination-current,
.campaign__swiper-button-wrap .swiper-pagination-total {
